import React from 'react'
import PropTypes from 'prop-types'
import Logo from '../../../assets/logo.svg'

import { Link } from 'gatsby'

import classNames from 'classnames'
import styles from './Hero.module.scss'

const Hero = ({ targetRef, teamRef }) => ((
  <div ref={targetRef} className={styles.hero}>
    <div className={styles.overlay} />
    <div className={styles.background} />
    <picture className={styles.siteLogo}>
      <img src={Logo} alt="logo" />
    </picture>
    <div className={styles.heroInnerContainer}>
      <h1 className={styles.header}>
        What&apos;s up!
      </h1>
      <p className={styles.description}>
        Hello! My name is Sheng Chuang, and I am a passionate full stack engineer who currently locating at 1.3521° N, 103.8198° E.
      </p>
      <button
        type="button"
        className={styles.button}
        onClick={() => window.scrollTo({
          top: teamRef.current.offsetTop,
          left: 0,
          behavior: 'smooth'
        })}
      >
        More informations
      </button>
      <Link style={{ textDecoration: 'none' }} to="/blog">
        <button
          type="button"
          className={classNames([styles.button, styles.secondary])}
        >
          Visit my blog &rarr;
      </button>
      </Link>
    </div>
  </div>
))

Hero.propTypes = {
  targetRef: PropTypes.object, // eslint-disable-line
  teamRef: PropTypes.object, // eslint-disable-line
}

export default Hero